.item {
  background-color: #fff;
  border-radius: 1rem;
  padding: 1rem 2rem;
  width: 50vw;
  display: flex;
  justify-content: space-between;
  margin: 1rem auto;
}

.remove,
.clear,
.btn {
  outline: none;
  border: none;
  background: transparent;
  color: #178abe;
  cursor: pointer;
}

.item h3 {
  font-size: medium;
}

.clear,
.btn {
  color: #186b92;
  background: #186b92;
  width: fit-content;
  margin: 20px auto;
  border-radius: 10px;
  padding: 10px 2rem;
  color: #fff;
}
